@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Inter:1300,regular,500,600,700,800,900&display=swap&subset=cyrillic-ext");
:root {
  --font-body-color: rgb(0, 0, 0);
  --font-color-second: rgb(0, 70, 70);
  --font-color-title: #88c0ff;
  --font-color-active: #7796f3;
  --font-color-not-active: #8bb0ff6c;
  --background-color: #0e0e0e;
  --background-second: #020a10;
  --background-selected: #2b2d4156;
  --background-terminal: #16161e;
  --background-side-bar: #191b27;
  --background-active-bar: #1e202e;
  --background-color-title: #0e0e13;
  --background-transparent: #0e0e13da;
  --selected-color: #71cbff;
  --themes-color: #5e4da8;
  --background-thumb: #16161e;
  --scrollbar-thumb-bgcolor: rgb(71, 83, 45);
  --pos: center;
  --size: 0;
  --inset-top: 0;
  --inset-right: 100%;
  --inset-bottom: 100%;
  --inset-left: 0;
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.gradient-services, .preloader {
  background: linear-gradient(-30deg, #004646, #81eeee 50%, #004646);
}

html {
  font-style: normal;
  font-size: 62.5%;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  background: #fbf9f4;
  height: 100%;
  counter-reset: heading;
  color: rgb(0, 0, 0);
}
@media (max-width: 1024px) {
  html {
    font-size: 50%;
  }
}

body {
  font-size: 1.5rem;
  font-weight: 400;
}
@media screen and (max-width: 690px) {
  body.no-scroll {
    overflow: hidden;
  }
}

* {
  padding: 0px;
  margin: 0px;
  border: none;
  box-sizing: border-box;
  scrollbar-width: thin;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  vertical-align: top;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

input,
button,
textarea {
  line-height: 100%;
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0.5rem;
}

label {
  cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: #9b9999;
  background-color: rgba(0, 84, 99, 0.2470588235);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: #9b9999;
  background-color: rgba(0, 84, 99, 0.2470588235);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
  background: transparent;
}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

@font-face {
  font-family: "Arial";
  font-display: swap;
  src: url("../fonts/Arial.eot") format("eot"), url("../fonts/Arial.woff") format("woff"), url("../fonts/Arial.woff2") format("woff2"), url("../fonts/Arial.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
