.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
}

[class*=_container] {
  max-width: 1470px;
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 10px;
}

[class*=icon-] {
  cursor: pointer;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu-catalog__parent-menu > ._collapse:not(._show) {
  display: none;
}

.select > ._collapse:not(._show) {
  display: none;
}

@media (max-width: 690px) {
  .catalog__categories > ._collapse:not(._show) {
    display: none;
  }
}
.login-container__body {
  position: relative;
  padding-left: 2rem;
  width: 100%;
  height: 100%;
  gap: 2rem;
  display: grid;
  grid-template-columns: auto minmax(13.048rem, max-content);
  justify-content: space-between;
  z-index: 9;
}
@media screen and (max-width: 960px) {
  .login-container__body {
    justify-content: end;
  }
}
.login-container__login {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.login-container__login [class*=icon-] {
  margin-right: 5px;
  font-size: 2.7rem;
}
.login-container__list {
  cursor: pointer;
  width: auto;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 690.98px) and (max-width: 960px) {
  .login-container__list {
    padding: 2rem;
    pointer-events: none;
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0;
    transform: translateY(60%);
    border: 1px solid #004646;
    background: #fbf9f4;
    border-radius: 10px;
    transition: all 0.5s ease-in-out;
  }
  .login-container__list._visible {
    pointer-events: all;
    opacity: 1;
    display: flex;
  }
}
.login-container__list span {
  margin-right: 1rem;
  line-height: 170%;
}
.login-container__link {
  white-space: nowrap;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
}
.login-container__link:not(:last-child) {
  margin-right: 1rem;
}
@media (min-width: 690.98px) {
  .login-container__link:hover {
    font-weight: 700;
    font-size: 1.456rem;
  }
}
@media (max-width: 690px) {
  .login-container__link {
    font-size: 1.7rem;
    line-height: 150%;
  }
}
@media (min-width: 960.99px) {
  .login-container__link {
    line-height: 0;
  }
  .login-container__link:first-child {
    padding-left: 0.5rem;
  }
}
.login-container__user-button._icon {
  pointer-events: none;
}
@media (max-width: 690px) {
  .login-container__user-button {
    display: none;
    pointer-events: none;
  }
}
.login-container__phone {
  white-space: nowrap;
  justify-self: end;
  align-self: center;
}
.login-container__phone a {
  line-height: 120%;
}
.login-container__phone a:hover {
  line-height: 110%;
  font-weight: 700;
  font-size: 1.455rem;
  letter-spacing: -0.4px;
}
.swiper-wrapper {
  align-items: flex-start;
}

.title-text__text {
  position: relative;
  display: flex;
  align-items: center;
  color: rgb(0, 70, 70);
  font-size: 3rem;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 100%; /* 36px/30px */
  text-align: center;
}
@media (max-width: 425px) {
  .title-text__text {
    font-size: 2.3rem;
  }
}
.title-text__text::before, .title-text__text::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  background: url("../img/icons/brand.png") center/contain no-repeat;
}
@media (max-width: 490px) {
  .title-text__text::before, .title-text__text::after {
    display: none;
  }
}
.title-text__text::before {
  left: -13px;
  transform: translateX(-100%);
}
.title-text__text::after {
  right: -13px;
  transform: translateX(100%);
}

._ibg {
  position: relative;
  height: 100%;
  width: 100%;
}
._ibg img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: fill;
}

._lnb {
  position: relative;
}
._lnb::before {
  display: block;
  content: attr(title);
  font-weight: 700;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}

@media (min-width: 920.99px) {
  ._clip-circle {
    position: relative;
    z-index: 2;
  }
  ._clip-circle:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    background-color: #004646;
    clip-path: circle(var(--size) at var(--pos));
    transition: all 0.5s ease-in-out;
    z-index: 1;
    border-radius: 32px;
  }
  ._clip-circle:hover a {
    color: #fbf9f4;
  }
  ._clip-circle:hover:before {
    --size: 235px;
  }
}

.checkbox._error .checkbox__lable::before {
  box-shadow: 0px 0px 14px 3px #f16666;
}
.checkbox__input {
  display: none;
}
.checkbox__input:checked + .checkbox__label .icon-arow-down {
  display: block;
  scale: 1;
  opacity: 1;
}
.checkbox__checed {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 140%;
}
.checkbox__label {
  max-width: 250px;
  cursor: pointer;
  position: relative;
  display: inline-flex;
  align-items: center;
  font-size: 16px;
  line-height: 110%;
}
.checkbox__label .icon {
  margin-right: 0.5rem;
  margin-left: -3.2rem;
  height: 3.5rem;
}
@media (max-width: 1024px) {
  .checkbox__label .icon {
    margin-left: -3.6rem;
  }
}
.checkbox__label .icon-arow-down {
  position: absolute;
  position: relative;
  display: flex;
  align-items: center;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  height: 28px;
  font-size: 2.8rem;
  color: #2cc55f;
}
.checkbox__label::before {
  content: "";
  position: relative;
  margin-right: 10px;
  flex: 0 0 20px;
  height: 20px;
  align-self: center;
  border: 1px solid #004646;
  border-radius: 7px;
  transition: box-shadow 0.3s ease-in-out;
}
.checkbox__label a {
  text-decoration: underline;
  font-weight: 600;
}
.checkbox__label a:hover {
  text-decoration: none;
}
.checkbox__label span {
  font-size: 10px;
}

.product-offer__title--favourites {
  display: flex;
  justify-content: center;
}

.header {
  max-width: 1470px;
  margin: 0 auto;
}
.header__header-wrapper {
  background: #fbf9f4;
}
.header__top-menu {
  border-bottom: 1px solid #004646;
  padding: 0.75rem 0;
  display: grid;
  justify-content: space-between;
  align-items: center;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 690px) {
  .header__top-menu {
    grid-template-columns: min-content 1fr min-content;
    justify-items: center;
    padding: 8px 0 6px;
    border: none;
  }
}
@media (min-width: 690.98px) {
  .header__top-menu .header__column:nth-child(2) {
    display: none;
  }
}
.header__column {
  display: flex;
  flex-direction: column;
  flex: 0 1 50%;
}
@media (max-width: 690px) {
  .header__column:nth-child(3) {
    justify-self: end;
  }
}
.header__column .login-container__user-button {
  pointer-events: none;
}
@media screen and (max-width: 960px) {
  .header__column .login-container__user-button {
    pointer-events: all;
  }
}
.header__column .login-container__fade-in {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
  transform: translateY(70%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 2rem;
  border: 1px solid #004646;
  background: #fbf9f4;
  border-radius: 5px;
  height: 50px;
  z-index: 8;
  width: auto;
}
@media screen and (max-width: 960px) {
  .header__column .login-container__fade-in {
    pointer-events: none;
    box-shadow: 1px 1px 3px #000;
    transition: all 0.5s ease-in-out;
  }
  .header__column .login-container__fade-in._visible {
    pointer-events: all;
    opacity: 1;
    display: flex;
  }
}
.header__middle-menu {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 690px) {
  .header__middle-menu {
    display: none;
  }
}
.header__menu-bottom {
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5215686275);
  width: 100%;
  border-radius: 10px;
  color: #e1b69e;
  background: linear-gradient(90deg, rgb(5, 52, 42) -23.739%, rgb(4, 61, 49) 11.827%, rgb(3, 84, 66) 100%);
}
@media (max-width: 690px) {
  .header__menu-bottom {
    display: none;
  }
}
.header__button-sidebarcatalog {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1px solid #004646;
}
.header__button-sidebarcatalog ._icon {
  color: #004646;
}
@media (min-width: 690.98px) {
  .header__button-sidebarcatalog {
    display: none;
  }
}
@media (min-width: 690.98px) {
  .header__button-menu {
    display: none;
  }
}

@media (max-width: 690px) {
  .main-menu {
    display: none;
  }
}
.main-menu__body {
  display: flex;
  max-width: 54rem;
  justify-content: space-between;
}
@media screen and (max-width: 920px) {
  .main-menu__body li {
    margin-left: 2.5rem;
  }
  .main-menu__body li:first-child {
    margin-left: 0;
  }
}
.main-menu__link {
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
}
.main-menu__link:hover {
  font-weight: 700;
}

@media (max-width: 690px) {
  .service-menu {
    display: none;
  }
}
.service-menu__body {
  padding-left: 2rem;
  display: grid;
  justify-content: space-between;
  align-items: center;
  grid-template-columns: auto minmax(13.048rem, max-content);
}
@media screen and (max-width: 960px) {
  .service-menu__body {
    justify-content: end;
  }
}
.service-menu__column {
  width: 100%;
  align-items: center;
  display: flex;
}
.service-menu__column:nth-child(1) {
  justify-content: space-between;
}
@media screen and (max-width: 960px) {
  .service-menu__column:nth-child(1) {
    justify-content: flex-end;
    padding-right: 2.4rem;
  }
}
.service-menu__column:nth-child(2) {
  justify-content: flex-end;
}
@media screen and (max-width: 960px) {
  .service-menu__column {
    justify-content: end;
