  align-items: center;
  justify-content: flex-end;
}
.service-menu__link .icon-shopping-cart::before {
  font-size: 3rem;
}
.service-menu__link [class*=icon-] {
  font-size: 2.7rem;
}
@media (max-width: 690px) {
  .service-menu__link {
    justify-content: flex-end;
  }
}
.service-menu__link:nth-child(2) {
  margin-left: 4.4rem;
}
.service-menu__text {
  font-size: 1.5rem;
  height: 20px;
  line-height: 0;
  padding: 0 0.5rem;
  white-space: nowrap;
  display: flex;
  align-items: center;
}
.service-menu__text:hover {
  font-weight: 700;
  font-size: 1.465rem;
}
@media screen and (max-width: 960px) {
  .service-menu__text:not(:last-child) {
    display: none;
  }
}

.logo-container {
  display: flex;
  align-items: center;
}
.logo-container__brand {
  height: 4rem;
  width: 4rem;
  display: flex;
  align-items: center;
}
.logo-container__image img {
  width: 15rem;
  height: 4rem;
  object-fit: contain;
}

.middle-menu__body {
  max-width: 54rem;
  display: flex;
  justify-content: space-between;
}

.anchor-link__link:hover {
  font-weight: 700;
}

.footer__body {
  padding: 5.7rem 3.4rem;
  display: grid;
  gap: 3rem;
  grid-template-columns: 1fr 1fr 1fr min-content;
}
@media (max-width: 768px) {
  .footer__body {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    row-gap: 2rem;
    column-gap: 0;
  }
}
@media (max-width: 375px) {
  .footer__body {
    padding: 5.7rem 1.5rem;
  }
}
.footer__column ul {
  color: #e1b69e;
  font-size: 1.6rem;
  font-weight: 400;
}
@media (max-width: 425px) {
  .footer__column ul {
    font-size: 11px;
  }
}
.footer__column ul:not(:last-child) {
  margin-bottom: 2rem;
}
.footer__column li {
  white-space: nowrap;
}
.footer__column:nth-child(1) {
  display: flex;
  flex-direction: column;
}
.footer__column:nth-child(1) li {
  line-height: 170%;
}
@media (max-width: 768px) {
  .footer__column:nth-child(1) {
    grid-column: 2/3;
    grid-row: -3/-2;
  }
}
@media (max-width: 425px) {
  .footer__column:nth-child(1) {
    margin-left: calc(-15px + 16 * (100vw - 320px) / 105);
  }
}
.footer__column:nth-child(2) li {
  line-height: 130%;
}
@media (max-width: 425px) {
  .footer__column:nth-child(2) li {
    line-height: 170%;
  }
}
.footer__column:nth-child(3) {
  line-height: 170%;
}
@media (max-width: 768px) {
  .footer__column:nth-child(3) {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
.footer__column:nth-child(4) {
  display: flex;
  flex-direction: column;
  justify-self: end;
}
