.button-menu {
  position: sticky;
  top: 0rem;
  left: 0;
  transform: translateX(var(--mob-hidden-width));
}
.button-menu__items {
  overflow: hidden;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  border: 1px solid #004646;
  cursor: pointer;
}
.button-menu__items svg {
  width: 4rem;
  height: 4rem;
  stroke: #004646;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none;
  display: block;
}
.button-menu__items svg path {
  transition: stroke-dasharray var(--duration, 0.4s) var(--easing, ease) var(--delay, 0s), stroke-dashoffset var(--duration, 0.4s) var(--easing, ease) var(--delay, 0s);
  stroke-dasharray: var(--array-1, 26px) var(--array-2, 100px);
  stroke-dashoffset: var(--offset, 126px);
  transform: translateZ(0);
}
.button-menu__items svg path:nth-child(2) {
  border: 1px solid #004646;
  --duration: 0.35s;
  --easing: ease-in;
  --offset: 100px;
  --array-2: 74px;
}
.button-menu__items svg path:nth-child(3) {
  --offset: 133px;
  --array-2: 107px;
}
.button-menu._active svg {
  stroke: #004646;
}
.button-menu._active svg path {
  --offset: 57px;
}
.button-menu._active svg path:nth-child(1), .button-menu._active svg path:nth-child(3) {
  --delay: 0.15s;
  --easing: cubic-bezier(0.2, 0.4, 0.2, 1.1);
}
.button-menu._active svg path:nth-child(2) {
  --duration: 0.4s;
  --offset: 2px;
  --array-1: 1px;
}
.button-menu._active svg path:nth-child(3) {
  --offset: 58px;
}

